<i18n>
{
    "en": {
        "weight": "Weight"
    },
    "nl": {
        "weight": "Gewicht"
    }
}
</i18n>

<template>
    <table-cell-input @edit="onEdit" @cancel="$emit('cancel')">
        <template v-slot:content>
            <label for="weight" class="u-font-medium">{{ $t('weight') }}:</label>
            <div class="u-relative">
                <input id="weight" class="percent-input u-font-normal" ref="weight-input" type="number" min="0" max="100" :value="itemWeight|formatNum" autocomplete="off" @keyup.enter="onEdit" @keyup.esc="$emit('cancel')">
                <div class="percent"><i class="fa fa-percent" aria-hidden="true"></i><span class="sr-only">%</span></div>
            </div>
        </template>
    </table-cell-input>
</template>

<script lang="ts">
import {Component, Prop, Vue} from 'vue-property-decorator';
import TableCellInput from './TableCellInput.vue';

@Component({
    name: 'weight-input',
    components: { TableCellInput },
    filters: {
        formatNum: function (v: number|null) {
            if (v === null) { return ''; }
            return v.toLocaleString(undefined, {maximumFractionDigits: 2});
        }
    }
})
export default class WeightInput extends Vue {
    @Prop({type: Number, default: ''}) readonly itemWeight!: number|null;

    get weightInput() {
        return this.$refs['weight-input'] as HTMLInputElement;
    }

    onEdit() {
        const el = this.weightInput as HTMLInputElement;
        if (!el.checkValidity()) {
            el.reportValidity();
            return;
        }
        const value = parseFloat(this.weightInput.value);
        this.$emit('ok', isNaN(value) ? null : value);
    }

    mounted() {
        this.$nextTick(() => this.weightInput.focus());
    }
}
</script>

